body {
  background-image: url('./images/capa.jpg');
  background-size: cover;
}

.title-pokedex {
  color: red;
  font-size: 50px;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #000;
}

.container-links {
  display: flex;
  justify-content: space-around;
  width: 400px;
}

.container-link {
  border-radius: 15px;
  background-color: rgb(0, 0, 0, 0.9);
}

.link {
  color: blue;
  text-decoration: none;
  font-size: 20px;
  padding: 2px 15px;
}

.App {
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;
}

@media screen and (max-width: 500px) {
  body {
    min-height: 640px;
  }
  .title-pokedex {
    font-size: 30px;
  }

  .container-link {
    padding: 5px;
    width: 80px;
  }

  .link {
    font-size: 15px;
    padding: 0;
  }

  .container-links {
    width: 300px;
  }

  .subtitle-pokedex {
    font-size: 20px;
  }

  .pokedex-buttons-panel {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 15px 2px;
  }

  .filter-button {
    font-size: 10px;
    height: 25px;
    margin: 5px;
    width: 50px;
  }

  .pokemon {
    width: 300px;
    padding: 5px;
  }

  .p-style {
    font-size: 10px;
  }

  .img-pokemon {
    width: 100px;
  }

  .pokedex-button {
    font-size: 15px;
    height: 35px;
    width: 100px;
  }

  .p-text {
    font-size: 12px;
    max-width: 280px;
  }

  .container-about {
    align-items: center;
    display: flex;
    flex-direction: column;
  }
}
