.pokemon {
  background-color: rgb(255, 255, 255, 0.5);
  align-items: center;
  border: 1px gray solid;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  margin: auto;
  min-width: 25%;
  width: 500px;
  padding: 2px 20px;
}

.pokemon-overview {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 200px;
}

.p-style, .link-style {
  font-weight: 500;
  text-decoration: none;
}

.favorite-icon {
  align-self: flex-start;
  margin: 10px;
  width: 20px;
}
