.pokedex {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
}

.subtitle-pokedex {
  color: red;
  font-size: 30px;
  -webkit-text-stroke-color: #000;
  -webkit-text-stroke-width: 2px;
}

.pokedex-buttons-panel {
  display: flex;
  flex-flow: row wrap;
  margin: 15px 2px;
}

.container-btns {
  display: flex;
  justify-content: space-between;
  width: 300px;
}

.pokedex-button {
  background-color: rgb(0, 128, 0, 0.8);
  border-radius: 5px;
  height: 40px;
  width: 100px;
}

.pokedex-button:disabled {
  background-color: #d0d0d0;
}

.filter-button {
  background-color: rgb(0, 0, 0, 0.8);
  border-radius: 5px;
  height: 35px;
  margin: 10px;
  width: 80px;
}
